<template>
  <div class="main">
    <div class="welcome">
      <div class="person">
        <ul class="header flex">
          <li class="headerImg">
            <div class="imgs">
              <img alt src="../assets/img/headerimg.png" v-if="!headerImg">
              <img alt :src="headerImg" v-else>
            </div>
          </li>
          <li class="headerName">
            <div class="outLogin">
              <yd-icon name="share2" size=".4rem" @click.native="outGoing()"></yd-icon>
            </div>
            <div class="qrcode clearfixed">
              <span class="icon-qrcode" v-if="levels=='small_work'" @click="showSvg()"></span>
              <span class="icon-blank" v-else></span>
            </div>
            <div class="name">{{realName}}</div>
            <div style="opacity:0.9;">上海国际雅兰美容运营集团</div>
            <div style="opacity:0.9;">Shanghai International Yalan Beauty Operating Group Co.</div>
            <div class="bindWechat">
              <button class="wechatBtn" @click="bindWechats()"><span class="icon-wechat"></span>绑定微信</button>
            </div>
          </li>
        </ul>
      </div>
      <div class="nav" @click="linkTo('/todayIncome')">
        <p class="small">{{xztime}}</p>
        <p class="money">{{todayIncome|tofixFour}}
          <span style="font-size:0.28rem;">元</span>
        </p>
        <p class="small">今 日 收 入 Today's Income</p>
      </div>
      <div class="linkBtn" v-if="!bindBtns">
        <ul class="ulBtn">
          <li @click="linkTo('/wallet')">
            <div class="boxLi">
              <div class="icon icon-money"></div>
              <p>钱 包</p>
            </div>
          </li>
          <li @click="linkTo('/history')" v-if="levels!='small_work'">
            <div class="boxLi">
              <div class="icon icon-history"></div>
              <p>设备启动记录</p>
            </div>
          </li>
          <li @click="linkTo('/order')" v-else>
            <div class="boxLi">
              <div class="icon icon-order"></div>
              <p>订 单 记 录</p>
            </div>
          </li>
          <li @click="linkTo('/order')" v-if="levels!='small_work'">
            <div class="boxLi">
              <div class="icon icon-order"></div>
              <p>订 单 记 录</p>
            </div>
          </li>
          <li v-else></li>
        </ul>
        <ul class="ulBtn">
          <li @click="linkTo('/person')" v-if="levels!='small_work'">
            <div class="boxLi">
              <div class="icon icon-people"></div>
              <p>人 员 管 理</p>
            </div>
          </li>
          <li v-else></li>
          <li @click="linkTo('/device')" v-if="levels!='small_work'">
            <div class="boxLi">
              <div class="icon icon-device"></div>
              <p>设 备 列 表</p>
            </div>
          </li>
          <li v-else></li>
          <li v-if="levels=='store'">
            <div class="boxLi" @click="openBind">
              <div class="icon icon-bind"></div>
              <p>绑 定 设 备</p>
            </div>
          </li>
          <li v-else></li>
        </ul>
      </div>

      <div v-else align="center" class="bindDevices">
        <p>请输入要绑定的设备号</p>
        <input class="deviceSn_input" v-model="deviceSn" placeholder="设备编号" />
        <div class="flex">
          <span @click="bindDevice()">绑 定</span>
          <span @click="cencleBtn()">取 消</span>
        </div>
      </div>

      <div align="center" class="bottom_logo">
        <img src="../assets/img/logo.png" alt="" />
        <p>Shanghai International Yalan Beauty Operating Group Co.</p>
        <p>上海国际雅兰美容运营集团公司</p>
      </div>
    </div>

    <yd-popup class="qrcode_pop" v-model="qrsvgDialog" position="center" width="4rem">
      <div style="background-color:#fff;">
        <div class="qrcode_box">
          <dl v-html="this.qrSvg">
            {{this.qrSvg}}
          </dl>
        </div>
      </div>
    </yd-popup>
  </div>
</template>

<script>
  import ajax from "../ajax/ajax.js";
  import dataURL from "../server/dataUrl.js";
  import func from "../server/funcs";
  import auth from "../server/auth.js";
  export default {
    data() {
      return {
        bindBtns: false,
        deviceSn: "",
        realName: "姓名",
        headerImg: "",
        levels: "",
        qrsvgDialog: false,
        qrSvg: "",
        uid: "",
        balance: 0,
        xztime:"",
        year:"",
        month:"",
        date:"",
        todayIncome:0
      };
    },
    methods: {
      async showSvg() {
        let res = await ajax.Post(dataURL.main.creatOrder);
        this.qrSvg = res.qr;
        this.qrSvg = "<svg" + this.qrSvg.split("<svg")[1];
        this.qrsvgDialog = true;
      },
      linkTo(adr) {
        this.$router.push({
          path: adr
        });
      },
      openBind() {
        this.bindBtns = true;
      },
      cencleBtn() {
        this.bindBtns = false;
      },
      async bindDevice() {
        if (this.deviceSn == "") {
          this.$dialog.alert({ mes: "请输入设备编号" });
          return;
        }
        try {
          let res = await ajax.Post(dataURL.main.bind, {
            device_sn: this.deviceSn
          });
          this.$dialog.toast({
            mes: "绑定成功",
            timeout: 1500,
            icon: "success"
          });
          this.deviceSn = "";
        } catch (e) {
          this.$dialog.toast({
            mes: "绑定失败",
            timeout: 1500,
            icon: "error"
          });
        }
      },
      outGoing() {
        this.$dialog.confirm({
          title: "退出登录",
          mes: "确认退出?",
          opts: async () => {
            window.localStorage.removeItem("agency_class");
            window.localStorage.user_status = 'outlogin';
            window.location.reload();
          }
        });

      },
      bindWechats() {
        this.$dialog.confirm({
          title: "绑定微信",
          mes: "确认绑定此微信号?",
          opts: async () => {
            func.oauthUrlEncoding(this.uid);
          }
        });
      },
      async getUserinfo() {
        let res = await ajax.Get(dataURL.main.userInfo);
        console.log(res);
        this.realName = res.name;
        this.levels = res.level;
        if (res.wechat) {
          this.headerImg = res.wechat.headimgurl;
        }
        this.uid = res.id;
      },
      getTime() {
        var dateObj = new Date();
        this.year = dateObj.getFullYear();//年
        this.month = dateObj.getMonth() + 1;//月  (注意：月份+1)
        this.date = dateObj.getDate();//日
        var day = dateObj.getDay();
        var hours = dateObj.getHours();//小时
        var minutes = dateObj.getMinutes();//分钟
        var seconds = dateObj.getSeconds();//秒
        if (this.month < 10) {
          this.month = "0" + this.month;
        }
        if (this.date < 10) {
          this.date = "0" + this.date;
        }
        if (hours < 10) {
          hours = "0" + hours;
        }
        if (minutes < 10) {
          minutes = "0" + minutes;
        }
        if (seconds < 10) {
          seconds = "0" + seconds;
        }

        this.xztime = this.year + "/" + this.month + "/" + this.date + ",  " + hours + "：" + minutes + "：" + seconds;
        
      },
      async getTodayincome() {
        await this.getTime();
        let searchTime = this.year + "-" + this.month + "-" + this.date;
        let res = await ajax.Get(dataURL.main.todayIncome,{
          start_date:searchTime +" 00:00:00",
          end_date:searchTime + " 23:59:59"
        });
        this.todayIncome = res;
      }
    },
    filters: {
      tofixTwo: function (value) {
        return (parseFloat(value) / 100).toFixed(2);
      },
      tofixFour: function (value) {
        return (parseFloat(value) / 10000).toFixed(2);
      }
    },
    async created() {
      this.getUserinfo();
      this.getTodayincome()
    }
  };
</script>


